	import cmdProgress from "@/components/cmd-progress/cmd-progress.vue"
export default {  
	    data() {
	    	return {
				listnum:"",//选择题当前数据
	    		active: 1,//进度条
				molecule:1,//当前题数
				Denominator:0,//总题数
				choicechild:"",//最终选择内容
				indexColor:0,
				questionText:"",//当前题目
				selector:null,//当前选择题 选项
	    		list1: [{
	    			title: '已提交'
	    		}, {
	    			title: '已被接受'
	    		}]
	    	}
	    },  
	    components: {  
	        cmdProgress,  
	    },
		onLoad:function(){
			uni.request({
			    url: 'https://www.ccweb.xyz/gas/wx/api/getTopic', //
			    data: {},
			    success: (res) => {
					let item;
					console.log(res);
					this.Denominator=1;
					item=res.data.data[1];
					this.questionText=item.name;
					this.selector=item.option.split(";");
					// num=JSON.parse(item.option);
			        console.log(this.questionText);
					console.log(this.selector);
			    }
			});
			this.active=(this.molecule/this.Denominator)*100;
			
		},
		methods:{
			// 点击变色
			getTndex:function(indexx,e){
				this.indexColor=indexx;
				this.choicechild=e.currentTarget.dataset.ref;
				// return false;
			},
			// click:function(e){
			// 	console.log(e.currentTarget.dataset.ref);
			// },
			// 上一题
			pre:function(){
				this.molecule--;
				if(this.molecule<1){
					this.molecule=1;
					uni.showToast({
					    title: '已经第一题了哦',
					    duration: 1000
					});
				}
				this.active=(this.molecule/this.Denominator)*100;
			},// 
			// 下一题 或者 跳转填空题
			next:function(){
				// if 判断用户是否选择了选项
				if(this.choicechild!=""&&this.choicechild!=undefined){
				this.molecule++;
				// 当 当前页数大于总页数时
				if(this.molecule>this.Denominator){
					this.molecule=this.Denominator;
					uni.navigateTo({url: 'Completion',});
				}
				this.active=(this.molecule/this.Denominator)*100;
				}else{
					uni.showToast({
					    title: '选项为空，请重新检查',
					    duration: 1000
					});
				}
			},//
		}
		
	}
